
.EditGoal {
  padding: 0 30px 30px;
}
.EditGoal__Label {
  color: #b9b9b9;
  font: 600 16px "Rubik", Arial, sans-serif;
  width: 100%;
}
.EditGoal__Input {
  color: #b9b9b9;
  background: none;
  border: none;
  border-bottom: 1px #b9b9b9 solid;
  border-radius: 0;
  font: 300 16px "Rubik", Arial, sans-serif;
  height: 30px;
  margin-bottom: 30px;
  width: 100%;
}
.EditGoal__Input:focus {
  border-bottom-color: #40c5e7;
  outline: none;
}
.EditGoal__SubGoal,
.EditGoal__SubGoal--add {
  background: transparent;
  border: 1px #b9b9b9 solid;
  border-radius: 3px;
  color: #b9b9b9;
  font: 600 14px "Rubik", Arial, sans-serif;
  margin-bottom: 12px;
  padding: 10px;
  position: relative;
  width: 100%;
}
.EditGoal__SubGoal::after,
.EditGoal__SubGoal--add::after {
  border-radius: 50%;
  background-color: #ff3636;
  color: #fff;
  content: "x";
  font: 400 12px "Rubik", Arial, sans-serif;
  height: 15px;
  position: absolute;
  right: -7px;
  top: -7px;
  width: 15px;
}
.EditGoal__SubGoal:last-of-type,
.EditGoal__SubGoal--add:last-of-type {
  margin-bottom: 50px;
}
.EditGoal__SubGoal--add,
.EditGoal__SubGoal--add--add {
  background-color: #40c5e7;
  border-color: #40c5e7;
  color: #fff;
}
.EditGoal__SubGoal--add::after,
.EditGoal__SubGoal--add--add::after {
  display: none;
}
.EditGoal__Btn--submit {
  background: none;
  border: 1px #6ef83e solid;
  border-radius: 4px;
  color: #6ef83e;
  font: 600 16px "Rubik", Arial, sans-serif;
  padding: 20px 10px;
  width: 100%;
  transition: border, color 0.3s ease;
}
.EditGoal__Btn--submit:active {
  border-color: #58f720;
  color: #58f720;
  outline: none;
  transition: border, color 0.3s ease;
}


