
.Item__wrapper {
  background: transparent;
  border-bottom: 1px solid rgba(185,185,185,0.2);
  color: #b9b9b9;
  display: -ms-flexbox;
  display: flex;
  font: 400 1.5em/1em "Rubik", Arial, sans-serif;
  padding: 4vh 15px;
  text-decoration: none;
}
.Item__wrapper:hover {
  background: rgba(185,185,185,0.2);
}
.Item__icon {
  -ms-flex-preferred-size: 15%;
      flex-basis: 15%;
  font-size: 2em;
  text-align: center;
}
.Item__info {
  -ms-flex-preferred-size: 70%;
      flex-basis: 70%;
  padding: 0 15px;
}
.Item__progressbar {
  border-radius: 10px;
  height: 4px;
  margin-top: 0.5em;
  width: 0;
  transition: all 1.5s ease-in-out;
}
.Item__progressbar.Item__gray {
  background: #b9b9b9;
}
.Item__progressbar.Item__red {
  background: #e42d2c;
}
.Item__progressbar.Item__yellow {
  background: #f3f035;
}
.Item__progressbar.Item__green {
  background: #2ce46a;
}
.Item__progressbar span {
  display: block;
}
.Item__percentage {
  -ms-flex-preferred-size: 15%;
      flex-basis: 15%;
  font-weight: 200;
  text-align: center;
}


