
.ActionBar__actions {
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-between;
  -ms-flex-line-pack: justify;
  -ms-flex-pack: justify;
      justify-content: space-between;
  font-size: 4.5em;
  list-style: none;
  margin: 15px 0 30px;
  padding: 0 25px;
}
.ActionBar__action {
  color: #b9b9b9;
  cursor: pointer;
  display: block;
  position: relative;
  text-decoration: none;
  transition: color 0.3s ease;
}
.ActionBar__action:hover,
.ActionBar__action:active {
  color: #40c5e7;
  transition: color 0.3s ease;
}
.active {
  color: #40c5e7;
  cursor: pointer;
}
.circle_notification {
  background: #ff3636;
  border-radius: 50%;
  bottom: 0;
  color: #fff;
  display: inline-block;
  font-size: 13px;
  height: 14px;
  line-height: 0.9;
  padding: 0;
  position: absolute;
  right: 0;
  text-align: center;
  width: 14px;
}


