
.GoalNotifications {
  list-style: none;
  margin: 0;
  padding: 0;
}
.GoalNotification {
  padding: 0 25px;
  padding-bottom: 10px;
  transition: background 0.3s ease;
}
.GoalNotification:hover {
  background-color: #f2f1f1;
  transition: background 0.3s ease;
}
.GoalNotification__Title {
  color: #b9b9b9;
  font: 600 19px "Rubik", Arial, sans-serif;
  margin: 0;
}
.GoalNotification__Text {
  color: #b9b9b9;
  font: 300 14px "Rubik", Arial, sans-serif;
}
.GoalNotification__Icon {
  color: #b9b9b9;
  font-size: 26px;
}


