
.loading {
  background: rgba(255,255,255,0.8);
  height: 100%;
  width: 100%;
  color: #fff;
  overflow: hidden;
  position: fixed;
  z-index: 990;
  top: 0;
}
.loading_icon {
  top: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}


