
.Selector__wrapper {
  font-size: 10px;
  position: relative;
}
.Selector__wrapper::after {
  content: '\e90b';
  font-family: 'icomoon';
  display: block;
  position: absolute;
  top: 30px;
  right: 0px;
  width: 10px;
  height: 10px;
  z-index: 1;
}
.Selector__wrapperfixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 15;
}
.Selector__inputselector {
  font: 400 1.8em/1em "Rubik", Arial, sans-serif;
  width: 100%;
  height: 4em;
  background: transparent;
  color: #fff;
  border: 0;
  border-bottom: 1px solid #fff;
  margin-bottom: 30vh;
}
.Selector__autocomplete {
  background: #fff;
  color: #000;
  border-radius: 3px;
  padding: 5px 0px;
  position: absolute;
  top: 55px;
  left: 0;
  width: 100%;
  z-index: 20;
  overflow-y: scroll;
  max-height: 180px;
}
.Selector__autocomplete ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.Selector__autocomplete ul li {
  cursor: pointer;
  font-size: 1.3em;
  padding: 15px 0px;
}
.Selector__autocomplete ul li i {
  color: #40a2cb;
  padding-left: 15px;
  font-size: 1.5em;
  font-weight: 600;
}
.Selector__autocomplete ul li span {
  color: #40a2cb;
  font: 400 1.5em/1em "Rubik", Arial, sans-serif;
  padding: 0px 10px;
}
.Selector__autocomplete ul li:hover {
  background: rgba(64,162,203,0.2);
}
.expand-transition {
  transition: all 0.3s ease;
  height: 10px;
  padding: 10px;
  background-color: #fff;
  overflow: hidden;
}
.expand-enter,
.expand-leave {
  height: 0;
  padding: 0 10px;
  opacity: 0;
}
.form-inline .input-group .input-group-addon.Selector__groupaddon {
  width: 5%;
}


