
.Split {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-between;
  -ms-flex-line-pack: justify;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 5px;
  font: 10px/1.5 OpenSans;
}
.Split__Title {
  width: 100px;
  padding: 5px 0;
  padding-left: 0;
  font-size: 1.3em;
  color: #000;
}
.Split__Wrapper {
  position: relative;
  width: 100%;
  padding: 8px 0;
}
.Split__Bar {
  border-radius: 50px;
  width: 96%;
  height: 2px;
  margin: 4px auto 0;
  background-color: #fff;
}
.Split__BarInner {
  border-radius: 50px;
  position: relative;
  width: 60%;
  height: 100%;
  background-color: #fff;
}
.Split__BarInner--danger {
  background-color: #e74c3c !important;
}
.Split__BarInner::after {
  content: "";
  position: absolute;
  top: -7px;
  right: -7px;
  width: 15px;
  height: 15px;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 50%;
  background: #fff;
  box-shadow: none;
}
.Split__Range {
  position: absolute;
  top: 4px;
  left: 0;
  height: 30px;
  width: 100%;
  opacity: 0;
}
.Split__Range:disabled + .Split__BarInner::after {
  cursor: default;
}
.Split__Value {
  width: 55px;
  height: 30px;
  padding: 5px;
  font-size: 1.3em;
  text-align: center;
  color: #000;
  border: none;
  background-color: #eaeaea;
  box-shadow: none;
}
.Split__Value:disabled {
  background-color: #fff;
}
.Split__MoneyBlock {
  position: relative;
  height: 20px;
  font-size: 1.5em;
}
.Split:nth-child(3n - 1) .Split__BarInner {
  background: #fff;
}
.Split:nth-child(3n) .Split__BarInner {
  background: #fff;
}
.Split:nth-child(3n + 1):nth-child(even) .Split__BarInner {
  background: #fff;
}


