.Btn,
.Btn__clean--white,
.Btn__clean--blue,
.Btn__clean--small {
  background: transparent;
  border-radius: 3px;
  box-shadow: none;
  cursor: pointer;
  font: 300 13px "Rubik", Arial, sans-serif;
  outline: none;
  transition: all ease 0.3s;
}
.Btn:focus,
.Btn__clean--white:focus,
.Btn__clean--blue:focus,
.Btn__clean--small:focus {
  outline: none;
}
.Btn__clean--white {
  border: 2px solid #fff;
  color: #fff;
  min-width: 100px;
  padding: 10px 15px;
}
.Btn__clean--white:hover {
  background: #40a2cb;
  color: #fff;
}
.Btn__clean--blue {
  border: 2px solid #40a2cb;
  color: #40a2cb;
  min-width: 100px;
  padding: 10px 15px;
}
.Btn__clean--blue:hover {
  background: #40a2cb;
  color: #fff;
}
.Btn__clean--small {
  background: none;
  border: 1px #fff solid;
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  height: 40px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease;
  width: 100%;
}
.Btn__clean--small:active {
  background-color: #40c5e7;
  transition: background-color 0.3s ease;
}
