
.RegisterModal__wrapper {
  background: #3d9ee3;
  background: linear-gradient(to right, #3d9ee3 0%, rgba(61,84,232,0.7) 100%);
  color: #fff;
  font: 400 10px/1em "Rubik", Arial, sans-serif;
  left: 0;
  min-height: 100%;
  min-width: 100%;
  padding: 25px 15px;
  position: absolute;
  top: 0;
  width: 100%;
}
.RegisterModal__container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}
.RegisterModal__header {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 60px;
  padding-top: 25px;
}
.RegisterModal__header .RegisterModal__title {
  -ms-flex-preferred-size: 90%;
      flex-basis: 90%;
  font-size: 1.6em;
  font-weight: 600;
}
.RegisterModal__header .RegisterModal__close {
  -ms-flex-preferred-size: 10%;
      flex-basis: 10%;
  font-size: 1.5em;
  text-align: right;
}
.RegisterModal__content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}
.RegisterModal__Label {
  color: #fff;
  font-size: 1.6em;
  font-weight: 600;
}
.RegisterModal__textinput {
  background: transparent;
  border: 0 solid transparent;
  border-bottom: 1px solid #fff;
  color: #fff;
  font: 400 1.6em/1.5 "Rubik", Arial, sans-serif;
  height: 30px;
  margin-bottom: 30px;
  padding: 0;
}
textarea.RegisterModal__textinput {
  height: 100px;
}
.RegisterModal__textinput::-webkit-input-placeholder {
  color: #fff;
}
.RegisterModal__textinput::-moz-placeholder {
  color: #fff;
}
.RegisterModal__textinput:-ms-input-placeholder {
  color: #fff;
}
.RegisterModal__textinput::placeholder {
  color: #fff;
}
.RegisterModal__textinput:last-child {
  margin-bottom: 50px;
}
.RegisterModal__footer {
  display: -ms-flexbox;
  display: flex;
}
.Btn--subGoal {
  background: #fff;
  border: none;
  border-radius: 3px;
  color: #40c5e7;
  font: 400 14px "Rubik", Arial, sans-serif;
  margin-bottom: 10px;
  padding: 10px;
  position: relative;
  width: 100%;
}
.Btn--subGoal:last-of-type {
  margin-bottom: 30px;
}
.Btn--subGoal::after {
  background-color: #ff3636;
  border-radius: 50%;
  color: #fff;
  content: "x";
  display: block;
  height: 14px;
  line-height: 1;
  position: absolute;
  right: -7px;
  top: -7px;
  width: 14px;
}
.Btn__clean--white {
  background-color: #29abe1;
  border-width: 1px;
  font-size: 16px;
  font-weight: 600;
  padding: 22px 20px;
  width: 100%;
}


