
.GeneralNotifications,
.GeneralNotifications--active {
  background: #3d9ee3;
  background: linear-gradient(to right, #3d9ee3 0%, #3d54e8 100%);
  box-shadow: 3px 0 50px rgba(64,197,231,0.1);
  height: 100vh;
  left: 0;
  position: fixed;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
  top: 0;
  width: calc(100% - 40px);
  max-width: 400px;
  z-index: 1;
}
.GeneralNotifications--active,
.GeneralNotifications--active--active {
  transform: translateX(0);
  transition: transform 0.3s ease;
}
.GeneralNotifications--active .GeneralNotification__RedIcon::before,
.GeneralNotifications--active--active .GeneralNotification__RedIcon::before {
  transition: opacity 0.6s ease;
  opacity: 0;
}
.GeneralNotifications--active .GeneralNotification__RedIcon::after,
.GeneralNotifications--active--active .GeneralNotification__RedIcon::after {
  transition: opacity 0.6s ease;
  opacity: 1;
}
.GeneralNotifications__List {
  list-style: none;
  margin: 0;
  padding: 0;
}
.GeneralNotification {
  padding: 10px 25px;
  transition: background 0.3s ease;
}
.GeneralNotification:hover {
  background: #4da0e7;
  background: linear-gradient(45deg, #4da0e7 0%, #4d61eb 100%);
  transition: background 0.3s ease;
}
.GeneralNotification__Title {
  color: #fff;
  font: 600 19px "Rubik", Arial, sans-serif;
  margin: 0;
}
.GeneralNotification__Text {
  color: #fff;
  font: 300 14px "Rubik", Arial, sans-serif;
}
.GeneralNotification__Icon {
  color: #fff;
  font-size: 26px;
}
.GeneralNotification__MainTitle {
  color: #fff;
  font: 600 33px "Rubik", Arial, sans-serif;
  margin-bottom: 25px;
  padding-top: 45px;
  padding-left: 25px;
  position: relative;
}
.GeneralNotification__AlertIcon {
  color: rgba(255,255,255,0.3);
  font-size: 93px;
  position: absolute;
  overflow: hidden;
  right: 0;
  top: 0;
  width: 45px;
}
.GeneralNotification__RedIcon {
  height: 50px;
  position: absolute;
  right: -25px;
  top: 55px;
  width: 50px;
}
.GeneralNotification__RedIcon::before,
.GeneralNotification__RedIcon::after {
  height: 20px;
  left: calc(50% - 10px);
  position: absolute;
  top: calc(50% - 10px);
  width: 20px;
  transition: opacity 0.6s ease;
}
.GeneralNotification__RedIcon::before {
  background: #ff3636;
  border-radius: 50%;
  content: "";
  display: block;
  opacity: 1;
}
.GeneralNotification__RedIcon::after {
  color: #ff3636;
  content: "x";
  opacity: 0;
  line-height: 0.5;
}
@media (min-width: 900px) {
.GeneralNotification__RedIcon::before,
  .GeneralNotification__RedIcon::after {
    height: 30px;
    left: calc(50% - 10px);
    position: absolute;
    top: calc(50% - 10px);
    width: 30px;
    transition: opacity 0.6s ease;
}
.GeneralNotification__RedIcon::before {
    left: calc(50% - 15px);
}
}


